import React from "react";
import "./styles.css";
import firebase from "./firebase";

export default function App() {

  const [LED_D3, SetLED_D3] = React.useState([])
  const [LED_D4, SetLED_D4] = React.useState([])
  const GetData = () => {
    var starCountRef = firebase.ref('BatTatLed/LED_D3');
    starCountRef.on('value', function (snapshot) {
      SetLED_D3(snapshot.val())
      // updateStarCount(postElement, snapshot.val());
    });
    var starCountRef = firebase.ref('BatTatLed/LED_D4');
    starCountRef.on('value', function (snapshot) {
      SetLED_D4(snapshot.val())
      // updateStarCount(postElement, snapshot.val());
    });
  }
  React.useEffect(() => {
    GetData()
  })
  console.log(LED_D3 + " " + LED_D4)
  return (
    // secondary
    <div className="App">
      <h1>Điều kiển đèn LED</h1>
      <div className="row">
        <div className="col">
        <label className="">LED D3 {'trạng thái đang'} ({LED_D3 !== 'off' ? 'Bật' : 'Tắt'} {'   '}) </label>
          <button type="button" className="btn btn-primary" 
            onClick={async () => {
              var updates = {};
              updates['BatTatLed/LED_D3'] = LED_D3 === 'off' ? 'on' : 'off';
              var starCountRef = firebase.ref().update(updates);
              SetLED_D3(LED_D3 === 'off' ? 'on' : 'off')

          }}>
            {LED_D3 === 'off' ? 'Bật' : 'Tắt'}
          </button>
        </div>
      </div>
      <div className="row mt-3">
        <div className="col">
          <label className="">LED D4 {'trạng thái đang'} ({LED_D4 !== 'off' ? 'Bật' : 'Tắt'} {'   '}) </label>
        
          <button type="button" className="btn btn-primary" 
          onClick={async () => 
          { 
            var updates = {};
            updates['BatTatLed/LED_D4'] = LED_D4 === 'off' ? 'on' : 'off';
            var starCountRef = firebase.ref().update(updates);
            SetLED_D4(LED_D4 === 'off' ? 'on' : 'off')
          }}>
            {LED_D4 === 'off' ? 'Bật' : 'Tắt'}
          </button>
        </div>
      </div>
      <div className="row"></div>
    </div>
  );
}
